.homepage_section {
  font-family: "Segoe UI", sans-serif;
  line-height: 1.5;
  margin-top: -150px;
  animation: pop 0.25s ease-in-out;
}

@keyframes pop {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.presentation {
  width: fit-content;
  box-shadow: 0px 5px 20px 5px rgba(0, 0, 0, 0.5);
  padding: 5%;
  margin-left: 70%;
  transition: all 0.5s ease;
  align-items: center;
  text-align: center;
}

.presentation:hover {
  transform: scale(1.5);
  box-shadow: 0px 15px 40px 15px rgba(0, 0, 0, 0.5);
  cursor: pointer;
}

.welcome {
  font-size: 50px;
  font-weight: 600;
  margin-bottom: 10%;
}

.name {
  font-size: 30px;
  display: flex;
}

.porfolio {
  font-size: 20px;
}

.progress {
  width: 100%;
}

.ownerAndRight {
  position: fixed;
  top: 95%;
  left: 43%;
  font-size: x-small;
}

@media only screen and (max-width: 760px) {
  .welcome {
    margin-top: 20%;
    font-size: 25px;
  }

  .name {
    font-size: 18px;
    display: flex;
  }

  .ownerAndRight {
    position: fixed;
    top: 95%;
    left: 43%;
    font-size: xx-small;
  }
}

@media only screen and (max-width: 420px) {
  .welcome {
    margin-top: 20%;
    font-size: 20px;
  }
  .name {
    font-size: small;
  }
  .presentation {
    font-size: small;
  }

  .porfolio {
    font-size: small;
  }
}
